* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  position: relative;
  min-height: 100%;
}

body {
  text-align: center;
  background-color: white;
}

.home, 
.main {
  font-size: 2rem;
  min-height: 76vh;
 
  background-color: white;
 
  display: flex;
  align-items: center;
  
  flex-direction: column;
  color: white;
}

h3 {
  position: absolute;
  margin-top: 37px;
  font-size: .9rem;
  color:#555;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: .5px 1.5px 3px lightblue;
  margin-left: 115px;
}

nav {
  /* display: flex;
  justify-content: space-around; */
  /* align-items: center; */
  height: 12vh;
  font-style: 1.2rem;
  background-color: whitesmoke;
  border-bottom: 3px solid #f4f4f4;
}

.nav-links {
  width: 40%;
  /* display: flex;
  justify-content: space-between; */
  margin-top: 37px;
  float: right;
  list-style: none;
  color: white;
  font-size: 1.2rem;
 
}

li {
  
  font-style: italic;
  color:#555;
  display: inline-block;
  margin-left: 25px;
   
}

li:hover {
border-bottom: 1px solid black;
color: gold;
}

a {
  text-decoration: none;
}
.burger-icon {
 display: none;
}


.Footer {
  
  /* display: flex;
  align-items: center;
