/* form styles .....................................................*/

.hero {
	text-align: center;
	padding: 10%;
	color: #fff;
	background-color: #520;
  background-image: url("./images/comb-350077_1920.jpg");
  background-size: cover;
  background-position: center;
	/*background-position: center 75%;
	background-repeat: no-repeat;
	background-size: cover; */
}
.hero h1 {
  color: goldenrod;
  font-size: 80px;
  font-weight: bolder;
}
.contact-flex-container {
  display: flex;
  flex-direction: row;
  align-content: space-around;
}
.left-container {
  height: 800px;
  width: 700px;
  background-color: purple;
  border: 1px solid #e9d8c8;
	border-radius: 6px;
}


form {
  width: 700px;
  background-color: silver;
  border: 1px solid #e9d8c8;
	border-radius: 6px;
  padding: 5%;
  /* margin-bottom: 20px; */
}
form p {
  text-align: justify;
  margin-bottom: 20px;
}
label, input {
	display: block;
}
label {
  text-align: left;
}
input, button {
	font-family: 'Abel', sans-serif;
	font-size: 19px;
}
input, textarea {
	width: 100%;
	padding: 10px;
	margin-top: 5px;
	margin-bottom: 30px;
	border: 1px solid #e9d8c8;
	border-radius: 6px;
}
button {
	border: 0;
	outline: none;
	cursor: pointer;
}
.contact {
  margin-bottom: 20px;
	cursor: pointer;
	background-color: #8842c2;
	color: #fff;
	padding-top: 6px;
	padding-bottom: 6px;
	padding-right: 12px;
	padding-left: 12px;
	border-radius: 4px;
	text-transform: uppercase;
	text-decoration: none;
}
.form-address {
  margin-top: 80px;
  margin-bottom: 40px;
}
.form-address > p, .form-call > p {
  color: white;
}

.form-call {
  margin-bottom: 40px;
}
.form-envelop > a {
  color: white;
}


